body {
    font: 64px "Century Gothic", Futura, sans-serif;
    color: #342929;
    text-shadow: #3d3d3d 0 0 2px;
    margin: 20px;
    background: #d3bd3e;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
    width: 450px;
    text-align: center;
}

.square {
    background: #d3bd3e;
    border: 2px solid #3d3d3d;
    text-shadow: #ffffff 0 2px 5px;
    float: left;
    font-size: 48px;
    height: 100px;
    width: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
}

.square:focus {
    outline: none;
}

.square:hover {
    cursor: pointer;
}

.game {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.game-info {
    margin-left: 20px;
    font-size: 20px;
    color: #3d3d3d;
    padding: 10px 20px;
    background: #f1e8bb;
}

.game-info > ul {
    list-style: none;
    padding: 0;
}

.game-info > div {
    padding-top: 20px;
}

.game-board > div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.moveButton {
    width: 100%;
    height: 30px;
    border: 0;
    background: #eeda80;
    margin: 5px 0;
    transition: .4s ease-in-out;
}

.moveButton:hover {
    border: 1px solid #884b03;
    background: #eeda80;
    transform: scale(1.04);
    cursor: pointer;
}